.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 8px 20px;
  min-height: 40px;

  font-family: "Oswald", sans-serif;
  font-size: 16px;
  text-decoration: none;
  border: none;
  cursor: pointer;
}

.button--long {
  width: 100%;
}
