.fieldset {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.fieldset__label {
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.fieldset__item {
  display: flex;
  align-items: center;
  position: relative;
  gap: 13px;
}

.fieldset__input {
  width: 100px;
  padding: 12px 15px;
  flex-grow: 1;

  font-size: 16px;
  line-height: 19px;
  border: 1px solid #e7e7e7;
  font-family: "Oswald", sans-serif;
  color: #444444;
}

.form__user-contacts .fieldset__input {
  padding: 14px 40px 14px 15px;
}

.fieldset__textarea {
  max-width: 280px;
  min-width: 280px;
  min-height: 150px;
  box-sizing: border-box;
  padding: 14px;

  font-family: "Oswald", sans-serif;
  font-size: 16px;
}

.fieldset__icon {
  position: absolute;
  right: 12px;
}
